import { configureStore, createSlice } from '@reduxjs/toolkit'

let user = createSlice({
    name: 'user',
    initialState: {name : 'kim', age : 20},
    reducers :{
        chageName(state){
            state.name = 'park'
        },
        addAge(state, a){
            state.age += a.payload
        }
    }
})

export let { addAge } = user.actions
export let { chageName } = user.actions

export default user