import { lazy, useState } from 'react';

import mainGround from './img/background.jpg'
import imageone from './img/마루_따봉.jpg'
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

import { Button, Nav, Navbar, NavDropdown, Container, Card, Placeholder, suspense, Form } from 'react-bootstrap';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom'
import axios from 'axios'

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import React from 'react';
import data from './data.js';

import { useUser } from './hooks/user.js'

const Detailpages = lazy(() => import('./pages/detail.jsx'));
const Monthlyevent = lazy(() => import('./pages/event.jsx'));
const Cart = lazy(() => import('./pages/cart.jsx'));

function App() {

  let [goods] = useState(data)
  let navigate = useNavigate();

  var watched = localStorage.getItem('watched');
  var watchedForstring = JSON.parse(watched)

  let [name, MyComponet] = useUser()

  return (
    <div className="App">

      <Navbar expand="lg" class="align-items-end">
        <Container>
          <Navbar.Brand href="/">DealerShip</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="/">메인 페이지</Nav.Link>
              <Nav.Link href="/cart">장바구니</Nav.Link>
              <Nav.Link href="/event">이달의 구매혜택</Nav.Link>
              <Nav.Link href="/user">Your account is {name} !</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <MyComponet />
      <Routes>

        <Route path="/" element={
          <div>
            <Goodscomponets goods={goods}></Goodscomponets>
          </div>
        } />
        <Route path="/detail/:id" element={
          <suspense fallback={<div>로딩중입니다</div>}>
            <Detailpages goods={goods} />
          </suspense>
        }>
        </Route>
        <Route path="/event" element={
          <Monthlyevent />
        } > <Route path="one" element={<div> 30만원 할인 </div>} />
          <Route path="two" element={<div> 첫 방문 시 교환건 증정 </div>} />
        </Route>
        <Route path="*" element={
          <div>Nothing in here !</div>
        }>
        </Route>
        <Route path="/cart" element={
          <Cart />
        }>
        </Route>
      </Routes>


      <Recentlylooked watchedForstring={watchedForstring} goods={goods} />
    </div>
  );
}

function Recentlylooked(props) {
  let [goods, setGoods] = useState(props.goods)
  let watchedForstring = props.watchedForstring
  let imageid = watchedForstring + 1

  let truegoods = goods.filter((good) => {
    return good.id == watchedForstring;
  })

  return (
    <div>
      <div className="d-flex justify-content-around">
        <Card style={{ width: '15rem' }}>
          <Card.Title>최근에 둘러본 상품</Card.Title>
          <Card.Img variant="top" src={'https://codingapple1.github.io/shop/shoes' + imageid + '.jpg'} />
          <Card.Body>
            <Card.Text>
              {truegoods[0]?.title}
            </Card.Text>
          </Card.Body>
        </Card>
      </div>

    </div>

  )
}

function Goodscomponets(props) {
  let [goods, setGoods] = useState(props.goods)

  return (
    <div className="App">
      <div className="main-bg" style={{ backgroundImage: 'url(' + mainGround + ')' }}></div>
      <Button variant="outline-info" onClick={() => {
        axios.get('https://codingapple1.github.io/shop/data2.json')
          .then((newData) => {
            //setgoodscopy(newData.data)
            setGoods([...goods, ...newData.data])
          })
          .catch(() => {
            alert('서버 사용량이 많습니다')
          })
      }}>서버 요청</Button>
      <Button variant="outline-danger">삭제</Button>
      <Container>
        <Row>
          {
            goods.map(function (lists, i) {
              let idforid = goods[i].id + 1
              return (
                <Col>
                  <img src={'https://codingapple1.github.io/shop/shoes' + idforid + '.jpg'}
                    onClick={() => {

                    }} width="70%"></img>
                  <div></div>
                  <a href={"/Detail/" + goods[i].id}> {goods[i].title}</a>
                  <p>{goods[i].content}</p>
                  <p>{goods[i].price}</p></Col>
              )
            })
          }
        </Row>
      </Container>
    </div>
  )
}

export default App;
